#section4 {
    width: 100%;
    .container {
        width: 100%;
        max-width: 1280px;
        margin: 0 auto;
        padding: 100px 0;
        position: relative;
        user-select: none;
        .h-group {
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-bottom: 30px;
            h2 {
                display: inline-flex;
                align-items: center;
                font-weight: 600;
                font-size: 24px;
                color: #000;
                line-height: 1.33em;
                letter-spacing: -0.01em;
                .live-time {
                    margin-left: 10px;
                    font-weight: 400;
                    font-size: 13px;
                    line-height: 1.23em;
                    color: #999;
                }
            }
            .btn-arr {
                display: inline-flex;
                align-items: center;
                font-size: 14px;
                line-height: 1.43em;
                letter-spacing: -0.01em;
                color: #000;
                overflow: visible;
                cursor: pointer;
                background: none;
                border: 0;
                border-radius: 0;
                outline: none;
                &::after {
                    flex: none;
                    display: block;
                    content: '';
                    width: 16px;
                    height: 16px;
                    margin-left: 5px;
                    background: url(../../../../public/images/arr_r16_black.png) no-repeat 50% 50%;
                }
            }
        }
        .tab-bar {
            margin-bottom: 25px;
            ul {
                display: flex;
                flex-wrap: wrap;
                position: relative;
                gap: 10px;
                li {
                    &.active {
                        button {
                            color: #000;
                            border-color: #000;
                            background: #fff;
                        }
                    }
                    &:hover {
                        button {
                            color: #00bc70;
                            border-color: #00bc70;
                            background-color: #fff;
                        }
                    }
                    button {
                        display: block;
                        padding: 6px 20px;
                        border: 1px solid #f5f5f5;
                        border-radius: 16px;
                        font-weight: 600;
                        font-size: 14px;
                        line-height: 1.29em;
                        color: #999e9c;
                        white-space: nowrap;
                        cursor: pointer;
                        background: #f5f5f5;
                        transition: color 0.75s, border-color 0.75s, background-color 0.75s;

                    }
                }
            }
        }
        .rank-cont {
            display: block;
            overflow: hidden;
            .slide-wrap {
                width: 1920px * 5;
                display: flex;
                align-items: flex-start;
                transform: translate3d(0, 0, 0);
                transition-duration: 0ms;
                position: relative;
                flex-wrap: nowrap;
                .slide {
                    width: 100%;
                    max-width: 1280px;
                    margin-right: 20px;
                    .product-list {
                        align-items: flex-start;
                        display: flex;
                        flex-wrap: wrap;
                        gap: 40px 20px;
                        .swiper-slide {
                            width: 240px;
                            .nail {
                                width: 100%;
                                height: 320px;
                                position: relative;
                                overflow: hidden;
                                &:hover {
                                    .img {
                                        .over-flip {
                                            height: 100%;
                                            opacity: 1;
                                        }
                                    }
                                    .btn-icon-box {
                                        transform: translateY(-100%);
                                    }
                                }
                                a {
                                    height: 100%;
                                    .sticker {
                                        position: absolute;
                                        left: 0;
                                        top: 0;
                                        z-index: 2;
                                        span {
                                            display: flex;
                                            align-items: center;
                                            justify-content: center;
                                            width: 32px;
                                            height: 32px;
                                            padding: 0;
                                            font-weight: 500;
                                            font-size: 14px;
                                            color: #fff;
                                            background-color: #000;
                                        }
                                    }
                                    .img {
                                        position: relative;
                                        width: 100%;
                                        height: 100%;
                                        img {
                                            width: 100%;
                                            height: 100%;
                                            position: absolute;
                                            left: 0;
                                            top: 50%;
                                            transform: translateY(-50%);
                                            background: #fff;
                                        }
                                        .over-flip {
                                            display: block;
                                            overflow: hidden;
                                            position: absolute;
                                            left: 0;
                                            top: 50%;
                                            z-index: 1;
                                            width: 100%;
                                            height: 0;
                                            transform: translateY(-50%);
                                            opacity: 0;
                                            transition: height 0.3s ease-in-out opacity 0.5s ease-in-out;
                                            will-change: height;
                                            img {
                                                width: 240px;
                                                height: 320px;
                                                position: absolute;
                                                left: 0;
                                                top: 50%;
                                                transform: translateY(-50%);
                                                background: #fff;
                                            }
                                        }
                                    }
                                }
                                .btn-icon-box {
                                    height: 60px;
                                    gap: 45px;
                                    display: flex;
                                    align-items: center;
                                    justify-content: center;
                                    position: absolute;
                                    left: 0;
                                    top: 100%;
                                    z-index: 3;
                                    width: 100%;
                                    transition: transform 0.3s ease-in-out;
                                    background-color: #00bc70;
                                    button {
                                        width: 32px;
                                        height: 32px;
                                        overflow: visible;
                                        cursor: pointer;
                                        border: 0;
                                        border-radius: 0;
                                        color: inherit;
                                        outline: 0;
                                        &.btn-heart {
                                            background: url(../../../../public/images/icon_heart_white.png) no-repeat center;
                                        }
                                        &.btn-cart {
                                            background: url(../../../../public/images/icon_cart_white.png) no-repeat center;
                                        }
                                        &.btn-buy {
                                            background: url(../../../../public/images/icon_buy_white.png) no-repeat center;
                                        }
                                    }
                                }
                            }
                            a {
                                pointer-events: none;
                                .info {
                                    margin-top: 20px;
                                    .name {
                                        display: -webkit-box;
                                        overflow: hidden;
                                        font-weight: 400;
                                        font-size: 14px;
                                        line-height: 1.43em;
                                        color: #666;
                                        text-overflow: ellipsis;
                                        -webkit-box-orient: vertical;
                                        -webkit-line-clamp: 2;
                                        strong {
                                            font-weight: 600;
                                            text-transform: uppercase;
                                        }
                                    }
                                    .price-box {
                                        margin-top: 10px;
                                        .price {
                                            font-weight: 700;
                                            font-size: 20px;
                                            line-height: 1.2em;
                                            color: #000;
                                        }
                                        .sale {
                                            margin-left: 10px;
                                            font-weight: 300;
                                            font-size: 20px;
                                            line-height: 1.2em;
                                            color: #f00;
                                        }
                                        .cost {
                                            margin-left: 15px;
                                            font-weight: 300;
                                            font-size: 14px;
                                            line-height: 1.29em;
                                            text-decoration-line: line-through;
                                            color: #999;
                                            letter-spacing: 0;
                                        }
                                        
                                    }
                                    .sticker-box {
                                        margin-top: 20px;
                                        display: flex;
                                        flex-wrap: wrap;
                                        gap: 1px;
                                        display: none;
                                        .sticker {
                                            display: inline-flex;
                                            align-items: center;
                                            height: 18px;
                                            padding: 0 5px;
                                            font-weight: 400;
                                            font-size: 10px;
                                            line-height: 1.4em;
                                            letter-spacing: 0;
                                            color: #00bc70;
                                            background: #e0fbf0;
                                        }
                                    }
                                    .star-grade {
                                        display: flex;
                                        align-items: center;
                                        margin-top: 15px;
                                        font-size: 12px;
                                        line-height: 1.33em;
                                        color: #999e9c;
                                        &::before {
                                            display: block;
                                            content: '';
                                            width: 12px;
                                            height: 12px;
                                            margin-right: 5px;
                                            background: url(../../../../public/images/ico_star.png) no-repeat 50% 50%;
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
            .slide-control {
                position: absolute;
                width: 100%;
                top: 50%;
                left: 0;
                button {
                    position: absolute;
                    top: 0;
                    width: 32px;
                    height: 32px;
                    background: url(../../../../public/images/arr_r32_black.png) no-repeat 50% 50%;
                    border: 0;
                    &.btn-prev {
                        left: -80px;
                        transform: rotate(180deg);
                    }
                    &.btn-next {
                        right: -80px;
                    }
                    &.disabled {
                        background: url(../../../../public/images/arr_r32_gray.png) no-repeat 50% 50%;
                    }
                }
            }
        }
    }
}